import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { Button } from '@material-ui/core';
import Product from '../components/Product';
import { products } from '../data/data';

import '../style/menu.css';

export default function Menu() {
  const [menu, setMenu] = useState(products);
  const history = useHistory();

  const toLogin = () => {
    history.push('/DelilahRestoApp');
  };

  const [isCartEmpty, setIsCartEmpty] = useState(false);

  const removeEmptyCartAlert = () => {
    setIsCartEmpty(false);
  };

  const toOrder = () => {
    let cart = JSON.parse(localStorage.getItem('cart'));
    if (cart.length === 0) {
      setIsCartEmpty(true);
    } else {
      history.push('/DelilahRestoApp/order');
    }
  };

  // const getMenu = async () => {
  //   let token = localStorage.getItem('token');
  //   const resp = await fetch('http://localhost:3001/menu', {
  //     method: 'GET',
  //     headers: {
  //       authorization: `Bearer ${token}`,
  //     },
  //   });
  //   const data = await resp.json();
  //   console.log(data);
  //   setMenu(data);
  // };

  useEffect(() => {
    // getMenu();
    console.log(menu);
  }, [menu]);

  return (
    <div className='menu'>
      <h3 className='title'>Nuestros platos</h3>
      {menu.map((element) => {
        return <Product key={element.id} {...element}></Product>;
      })}
      {isCartEmpty && <EmptyCart removeEmptyCartAlert={removeEmptyCartAlert} />}
      <Button className='submitBtn' variant='contained' onClick={toOrder}>
        CARRITO
      </Button>
      <Button className='linkBtn' variant='outlined' onClick={toLogin}>
        CERRAR SESI√ìN
      </Button>
    </div>
  );
}

const EmptyCart = ({ removeEmptyCartAlert }) => {
  useEffect(() => {
    setTimeout(() => {
      removeEmptyCartAlert();
    }, 3000);
  });
  return (
    <div>
      <h3 className='alert'>Carrito vac√≠o</h3>
    </div>
  );
};
