import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import '../style/menu.css';
import IconButton from '@material-ui/core/IconButton';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';

function Product(props) {
  const { id, img, name, price } = props;

  const [isBtn, setIsBtn] = useState(true);
  const location = useLocation();
  useEffect(() => {
    const location = window.location.pathname;
    const changeBtn = () => {
      if (location === '/DelilahRestoApp/menu') {
        setIsBtn(true);
      } else if (location === '/DelilahRestoApp/order') {
        setIsBtn(false);
      }
    };
    changeBtn();
  }, [location]);

  const addToCart = () => {
    const cart = JSON.parse(localStorage.getItem('cart'));
    console.log(cart);
    cart.push({ id, name, img, price });
    console.log('add ' + name + id);
    console.log(cart);
    localStorage.setItem('cart', JSON.stringify(cart));
  };

  const removeFromCart = () => {
    let cart = JSON.parse(localStorage.getItem('cart'));
    cart = cart.filter((product) => product.id !== id);
    console.log();
    console.log(cart);
    localStorage.setItem('cart', JSON.stringify(cart));
    window.location.reload();
  };

  return (
    <div className='productContainer'>
      <img src={img} alt='Food' />
      <h3>{name}</h3>
      <h6>${price}</h6>
      {isBtn ? (
        <IconButton className='addBtn' aria-label='delete' onClick={addToCart}>
          <AddCircleIcon fontSize='large' style={{ color: '#fc885c' }} />
        </IconButton>
      ) : (
        <IconButton
          className='addBtn'
          aria-label='delete'
          onClick={removeFromCart}
        >
          <HighlightOffIcon fontSize='large' style={{ color: '#3addda' }} />
        </IconButton>
      )}
    </div>
  );
}

export default Product;
