import React from 'react';
import { useHistory } from 'react-router-dom';
import { Button } from '@material-ui/core';
import './App.css';
import Login from './pages/Login';

export default function App() {
  const history = useHistory();

  const toRegister = () => {
    history.push('/DelilahRestoApp/register');
  };

  const toMenu = () => {
    localStorage.setItem('cart', JSON.stringify([]));
    history.push('/DelilahRestoApp/menu');
  };

  return (
    <div className='App'>
      <Login />
      <Button className='linkBtn' variant='outlined' onClick={toRegister}>
        REGISTRARSE
      </Button>
      <Button className='linkBtn' variant='outlined' onClick={toMenu}>
        Continuar sin usuario
      </Button>
    </div>
  );
}
