import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { Button } from '@material-ui/core';
import { Select, Input } from 'antd';
import { CaretDownOutlined } from '@ant-design/icons';

import '../style/order.css';

import Product from '../components/Product';

const { Option } = Select;

function Order() {
  const menu = JSON.parse(localStorage.getItem('cart'));

  const history = useHistory();
  const toMenu = () => {
    history.push('/DelilahRestoApp/menu');
  };
  const toConfirmed = () => {
    history.push('/DelilahRestoApp/confirmed');
  };

  function handleChange(value) {
    console.log(`selected ${value}`);
  }

  const [totalPrice, setTotalPrice] = useState(0);

  const addPrice = () => {
    let orderPrice = 0;
    for (var i = 0; i < menu.length; i++) {
      orderPrice += menu[i].price;
      setTotalPrice(orderPrice);
    }
  };

  useEffect(() => {
    addPrice();
  });

  return (
    <div className='order'>
      <section>
        <h3 className='title'>Detalle</h3>
        {menu.map((element) => {
          return <Product key={element.id} {...element}></Product>;
        })}
        <h6 className='totalPrice'>
          Total: <span>${totalPrice}</span>
        </h6>
      </section>
      <section>
        <h3 className='title'>Forma de pago</h3>
        <Select
          suffixIcon={<CaretDownOutlined />}
          className='selectPayment'
          defaultValue='Efectivo'
          style={{ minWidth: 50 }}
          onChange={handleChange}
          bordered={false}
          size='large'
          dropdownMatchSelectWidth={false}
        >
          <Option value='cash'>Efectivo</Option>
          <Option value='card'>Tarjeta d√©bito o cr√©dito</Option>
        </Select>
      </section>
      <section>
        <h3 className='title'>Direcci√≥n</h3>
        <Input
          placeholder='Direcci√≥n de domicilio'
          size='large'
          bordered={false}
        />
      </section>
      <Button className='submitBtn' variant='contained' onClick={toConfirmed}>
        CONFIRMAR PEDIDO
      </Button>
      <Button className='linkBtn' variant='outlined' onClick={toMenu}>
        VOLVER AL MENU
      </Button>
    </div>
  );
}

export default Order;
