import React from 'react';
import ReactDOM from 'react-dom';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'antd/dist/antd.css';
import './index.css';

import App from './App';
import Navbar from './components/Navbar';
import Register from './pages/Register';
import Menu from './pages/Menu';
import Order from './pages/Order';
import Confirmed from './pages/Confirmed';
import Follow from './pages/Follow';
import Manage from './pages/Manage';
import Error from './pages/Error';

ReactDOM.render(
  <React.StrictMode>
    <Router>
      <Navbar />
      <Switch>
        <Route exact path='/DelilahRestoApp' component={App} />
        <Route path='/DelilahRestoApp/register' component={Register} />
        <Route path='/DelilahRestoApp/menu' component={Menu} />
        <Route path='/DelilahRestoApp/order' component={Order} />
        <Route path='/DelilahRestoApp/confirmed' component={Confirmed} />
        <Route path='/DelilahRestoApp/follow' component={Follow} />
        <Route path='/DelilahRestoApp/manage' component={Manage} />
        <Route path='*' component={Error} />
      </Switch>
    </Router>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
