import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import '../style/navbar.css';

export default function Navbar() {
  const location = useLocation();
  const [navPage, setNavPage] = useState('navBarHome');
  console.log(location, navPage);

  useEffect(() => {
    const location = window.location.pathname;
    const changeNavClass = () => {
      if (location === '/DelilahRestoApp' || location === '/DelilahRestoApp/') {
        setNavPage('navBarHome');
      } else if (location === '/DelilahRestoApp/manage') {
        setNavPage('navBarManage');
      } else {
        setNavPage('navBarMenu');
      }
    };
    changeNavClass();
  }, [location]);

  return (
    <div className={navPage}>
      <h1 className='title'>Delilah Rest√≥</h1>
    </div>
  );
}
